import { f as fail, r as redirect } from "../../../chunks/index2.js";
import { l as login } from "../../../chunks/auth.js";
const actions = {
  default: async ({ request, cookies }) => {
    const data = await request.formData();
    const username = data.get("username");
    const password = data.get("password");
    if (!username || !password) {
      return fail(400, {
        error: "Username and password are required"
      });
    }
    const result = await login(username.toString(), password.toString());
    if (!result.success) {
      return fail(400, {
        error: result.message
      });
    }
    if (!result.sessionId) {
      return fail(500, {
        error: "Failed to create session"
      });
    }
    console.log("DEBUG: process.env.NODE_ENV =", process.env.NODE_ENV);
    console.log(
      "DEBUG: Cookie secure flag will be =",
      process.env.NODE_ENV === "production"
    );
    cookies.set("sessionId", result.sessionId, {
      path: "/",
      httpOnly: true,
      sameSite: "lax",
      // <-- CHANGE THIS TO LAX
      secure: false,
      // Keep this as is for now
      maxAge: 60 * 60 * 24 * 7
      // 1 week
    });
    throw redirect(303, "/dashboard");
  }
};
export {
  actions
};
